@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@300;400;700;900&display=swap);
@media (min-width: 1350px) {
    #header {
        position: fixed;
        left: 0px;
        top: 0px;

        width: 100%;
        height: 130px;

        display: flex;
        flex-direction: row;

        background-color: white;
        box-shadow: 0px 0px 15px -10px #383838;

        z-index: 10;
    }

    #header .left-panel {
        flex: 1 1;

        display: flex;
        align-items: center;
        justify-items: center;

        padding: 20px;
    }

    #header .left-panel .logo {
        width: 100%;
        height: 100px;
        object-fit: contain;
    }

    #logo-header-large {
        display: inline-block;
    }

    #button-header-large {
        display: inline-block;
    }

    #logo-header-medium {
        display: none;
    }

    #button-header-medium {
        display: none;
    }

    #header .middle-panel {
        display: flex;
        flex-direction: column;
    }

    #header .middle-panel .contact-panel {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
    }

    #header .middle-panel .contact-panel .item {
        color: #383838;
        font-size: 15px;
        font-weight: 700;

        margin: 20px 25px;
        margin-right: 50px;

        cursor: pointer;
    }

    #header .middle-panel .menu-panel {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;

        padding: 10px;
    }

    #header .middle-panel .menu-panel .item {
        color: #383838;
        font-size: 15px;
        font-weight: 700;
        text-decoration: none;

        padding: 10px 25px;
    }

    #header .middle-panel .menu-panel ion-icon {
        color: #383838;
        
        width: 18px;
        height: 18px;

        padding: 10px 25px;
    }

    #header .middle-panel .menu-panel .divider {
        width: 2px;
        height: 16px;

        background-color: rgba(0, 0, 0, 0.08);
    }

    #header .right-panel {
        display: flex;
        align-items: center;
        justify-content: center;

        flex: 1 1;
    }
}

