#footer {
    width: 100%;

    display: flex;
    flex-direction: column;

    background-color: #F5F5F5;
}

#footer .button-panel {
    width: 100%;

    display: flex;
    flex-direction: row;
    justify-content: flex-start;

    padding: 0px 0px 30px 50px;

    background-color: white;
}

#footer .button-panel div {
    margin-right: 30px;
}

#footer .top-panel {
    display: flex;
    flex-direction: row;
    justify-content: space-between;

    margin: 50px 120px 0px 120px;
    padding-bottom: 50px;

    border-bottom: 1px solid lightgray;
}

#footer .top-panel .column {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

#footer .top-panel .column .title {
