    font-size: 15px;
    font-weight: 400;

    padding: 3px 0px;
}

#footer .bottom-panel {
    display: flex;
    flex-direction: row;
}

#footer .bottom-panel .space {
    flex: 1 1;

    padding: 50px 0px 0px 120px;
}

#footer .bottom-panel .space .logo {
    width: 100%;
}

#footer .bottom-panel .middle {
    display: flex;
    flex-direction: column;
    align-items: center;

    padding: 50px 120px;
}

#footer .bottom-panel .middle .top {
    display: flex;
    flex-direction: row;
    align-items: center;
}

#footer .bottom-panel .middle .top .item {
    color: black;
    font-size: 15px;
    font-weight: 400;

    padding: 10px 25px;
}

#footer .bottom-panel .middle .top .divider {
    width: 2px;
    height: 40%;

    background-color: rgba(0, 0, 0, 0.08);
}

#footer .bottom-panel .middle .bottom {
