@media (min-width: 950px) {
    #home .slideshow {
        width: 100%;
        height: 80vh;

        background:linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.6)), url(/terramia/static/media/home-image.fe10d3b8.jpg);
        background-repeat: no-repeat;
        background-size: cover;

        display: flex;
        flex-direction: column;
        align-items: center;

        padding: 25px 200px;
    }

    #home .slideshow .title {
        color: white;
        font-size: 55px;
        font-weight: 700;
        text-align: center;

        margin-bottom: 30px;
    }

    #home .slideshow .text {
        color: lightgray;
        font-size: 25px;
        font-weight: 400;
        line-height: 40px;
        text-align: center;

        margin-bottom: 30px;
    }

    #home .slideshow .arrow {
        width: 70px;

        padding: 15px;

        border-radius: 10px;

        transition: all 100ms;

        cursor: pointer;

        align-self: center;
    }

    #home .slideshow .content .arrow:hover {
        background-color: rgba(255, 255, 255, 0.1);
    }
}

